import { createMuiTheme } from "@material-ui/core";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#0D6A88",
    },
  },
  mixins: {
    toolbar: {
      minHeight: 51,
    },
  },
  typography: {
    fontFamily: ["Roboto", "Helvetica", "Arial", "sans-serif"].join(","),
  },
});

theme.overrides = {
  MuiButton: {
    contained: {
      borderRadius: 40,
    },
  },
};

theme.props = {
  MuiAppBar: {
    position: "fixed",
    elevation: 0,
  },
  MuiToolbar: {
    variant: "dense",
  },
  MuiTextField: {
    margin: "none",
    variant: "outlined",
    size: "small",
    fullWidth: true,
  },
  MuiButton: {
    variant: "contained",
    color: "primary",
  },
};

export default theme;
