import { Switch, Route } from "react-router-dom";
import { Page } from "@components";
import { Page404, SignIn } from "@pages";

const App = (props) => (
  <Page {...props}>
    <Switch>
      <Route exact path="/" component={SignIn} />
      <Route path="" component={Page404} />
    </Switch>
  </Page>
);

export default App;
